<template>
<div class="flex-center full-width">
    <div class="card">
        <h1 class="text-center full-width font-weight-thin">
            Profile
        </h1>
        <div class="avatar-content">
            <Avatar :user-id="userId" size="150"/>
            <!--            <v-icon v-if="ghost"  large color="secondary"></v-icon>-->
            <div v-if="ghost" class="ghost">
                <PopupInfo title="Ghost"
                           text="Looks like you've got ghost status as you missed too much <span class='brand'>Talk</span>s. 
                           Now you are in low priority queue meaning you still can participate in conversations but have limited access. 
                           Please make sure you won't miss you next <span class='brand'>Talk</span> otherwise you might be banned to use entire platform for some time"
                           :icon="mdiGhost"
                           color="secondary"
                           large>
                </PopupInfo>
            </div>
            <div v-if="coins !== null" class="coins">
                <PopupInfo title="<span class='brand'>Talk</span> coins"
                           text="Total amount of coins you have at the moment. You'll spend it on your <span class='brand'>Talk</span>s.
                                There is no way to buy it. But you can earn it by just having conversation with others"
                           :chip="coins.toString()"
                           color="#FFD700"
                           dark>
                </PopupInfo>
            </div>
        </div>
        <div class="email">
            <div><b>{{email}}</b></div>
        </div>
        <div class="small">
            <div>{{userId}}</div>
        </div>

        <v-form ref="form" class="full-width" @submit.prevent="save">
            <v-text-field label="Name" v-model="name"
                          prepend-inner-icon="person" type="text"
                          hint="Will be shared with your peers during conversation"
                          :rules="[InputRules.required]"></v-text-field>
            <v-select multiple :items="languages" label="Native language(s)" v-model="nativeLanguages"
                      prepend-inner-icon="language"
                      :rules="[InputRules.required]"></v-select>

            <v-btn type="submit" :disabled="loading" block color="primary">Save</v-btn>
        </v-form>
    </div>
</div>
</template>

<script>
    import Avatar from "./Avatar";
    import InputRules from "../../services/InputRules"
    import UserProfileService from "../../services/UserProfileService";
    import UserService from "../../services/UserService";
    import {mdiGhost} from '@mdi/js';
    import PopupInfo from "../common/PopupInfo";

    export default {
        name: "UserProfile",
        components: {PopupInfo, Avatar},
        data: () => ({
            loading: true,

            name: null,
            email: null,
            nativeLanguages: null,
            languages: ['English', 'Spanish', 'Chinese', 'French', 'Italian', 'Russian', 'German', 'Japanese', 'Other'],
            coins: null,
            ghost: null,
            InputRules,

            mdiGhost
        }),
        async created() {
            try {
                let userProfile = await UserProfileService.getUserProfile();
                let coins = await UserProfileService.getCoins();

                const that = this;
                this.$nextTick(() => {
                    that.name = (userProfile.name) ? userProfile.name : null
                    that.email = (userProfile.email) ? userProfile.email : null
                    that.nativeLanguages = (userProfile.nativeLanguages) ? userProfile.nativeLanguages : null
                    that.coins = coins;
                })
            } finally {
                this.loading = false;
            }
        },
        computed: {
            userId() {
                return UserService.getUserId()
            }
        },
        methods: {
            async save() {
                if (!this.loading && this.$refs.form.validate()) {
                    try {
                        this.loading = true
                        await UserProfileService.updateUserProfile(this.name, this.nativeLanguages);

                        let to = this.$route.query.to
                        if (to) {
                            this.$router.push(to)
                        }
                    } catch (e) {
                        console.log(e)
                    } finally {
                        this.loading = false
                    }
                }
            }
        }
    }
</script>

<style lang="scss" scoped>

    .avatar-content {
        position: relative;

        .ghost {
            position: absolute;
            top: 0;
            right: 0;

            filter: drop-shadow(0 0 10px #999);
        }

        .coins {
            position: absolute;
            bottom: 0;
            right: 0;
        }
    }

</style>
